@font-face {
    font-family: "icons-turn-arrow";
    src: url("./fonts/icons/iconfont.eot") format("embedded-opentype"), url("./fonts/icons/iconfont.woff") format("woff"), url("./fonts/icons/iconfont.ttf") format("truetype"), url("./fonts/icons/iconfont.svg") format("svg");
}

.controller-unit {
    display: inline-block;
    width: 30px;
    height: 30px;
    margin: 0 5px;
    text-align: center;
    cursor: pointer;
    background-color: #aaa;
    border-radius: 50%;
    -webkit-transform: scale(0.5);
        -ms-transform: scale(0.5);
            transform: scale(0.5);
    vertical-align: middle;
    -webkit-transition: background-color 0.3s, -webkit-transform 0.6s ease-in-out;
    transition: background-color 0.3s, -webkit-transform 0.6s ease-in-out;
    -o-transition: transform 0.6s ease-in-out, background-color 0.3s;
    transition: transform 0.6s ease-in-out, background-color 0.3s;
    transition: transform 0.6s ease-in-out, background-color 0.3s, -webkit-transform 0.6s ease-in-out;
}

.is-center {
    background-color: #888;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
}

.is-center::after {
    color: #fff;
    font-family: "icons-turn-arrow";
    content: "\e63d";
    line-height: 30px;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.is-inverse{
    background-color: #555;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}


