html, body {
    width: 100%;
    height: 100%;
    background-color: #222222;
}

.content{
    width: 100%;
    height: 100%;
}

.stage{
    position: relative;

    width: 100%;
    height: 680px;
}

.img-sec{
    position: relative;

    width: 100%;
    height: 100%;
    overflow: hidden;
    -webkit-perspective: 1800px;
            perspective: 1800px;

    background-color: #dddddd;
}

.controller-nav{
    position: absolute;

    left: 0;
    bottom: 30px;
    z-index: 101;

    width: 100%;

    text-align: center;
}


